.navbar {
  background-color: #5b393f;
  padding: 10px;
  color: white;
}

.navbar ul {
  list-style-type: none;
  display: flex;
  justify-content: space-around;
}

.navbar li {
  margin: 0 10px;
}

.navbar a {
  color: white;
  text-decoration: none;
}

.navbar a:hover {
  text-decoration: none;
  cursor: pointer;
}

